import { Component, OnInit, Input, TemplateRef, ViewChild, ChangeDetectorRef, ChangeDetectionStrategy, ContentChild } from '@angular/core';
import { CometChatTabItem, TabAlignment } from '@cometchat/uikit-resources';
import { BaseStyle } from '@cometchat/uikit-shared';
import '@cometchat/uikit-elements'

@Component({
  selector: 'cometchat-tabs',
  templateUrl: './cometchat-tabs.component.html',
  styleUrls: ['./cometchat-tabs.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CometChatTabsComponent implements OnInit {
  @Input() tabAlignment:TabAlignment = TabAlignment.top;
  @Input() disableDragging:boolean = true
  @Input()  tabsStyle:BaseStyle = {height:"100%",width:"100%"}
  @Input() tabs:CometChatTabItem[] = [];
  @Input() keepAlive:boolean = false;
  public activeTab!:CometChatTabItem;
  public previousTab!:CometChatTabItem
  public childView!:TemplateRef<any> | null;
  constructor(private ref:ChangeDetectorRef){}
  openViewOnCLick = (tabItem:CometChatTabItem)=>{
    if(tabItem && tabItem.childView){
      this.childView = tabItem.childView;
      this.activeTab = tabItem
      this.ref.detectChanges()
    }


  }




  ngOnInit(): void {

  }

  ngOnChanges(){
    let index = this.tabs.findIndex((item: CometChatTabItem) => item.isActive === true)
    if(this.tabs.length > 0){
      this.openViewOnCLick(this.tabs[ index && index >= 0 ? index : 0])
    }
  }
   getButtonStyle(tab: CometChatTabItem) {
    const { style = {} } = tab || {};
    const { id } = this.activeTab || {};
    const active = id === tab?.id;
    var textStyle = tab.iconURL ? {} : {
      display:"flex",
      justifyContent:"center",
      alignItems:"center",
      padding:"7px"
    }
    return {
      background: active ? style.activeBackground  : style.background,
      buttonTextFont: active ? style.activeTitleTextFont  : style.titleTextFont,
      buttonTextColor: active ? style.activeTitleTextColor  : style.titleTextColor,
      buttonIconTint: active ? style.activeIconTint  : style.iconTint,
      height: style.height,
      width: style.width,
      border: style.border,
      borderRadius: style.borderRadius,
      gap: "0",
      padding: "0",
      cursor:"pointer",
      ...textStyle
    };
  }
  showTabs(tab:CometChatTabItem){
return {
  display: tab.id == this.activeTab.id ? "block" : "none"
}
  }
   getTabsStyle() {
    const alignment = (() => {
      switch (this.tabAlignment) {
        case TabAlignment.top:
          return {
            top: "0",
            left: "0",
          };
        default:
          return {
            bottom: "0",
            left: "0",
          };
      }
    })();
    return {
     background:this.tabsStyle.background,
     border:this.tabsStyle.border,
     borderRadius:this.tabsStyle.borderRadius,
      ...alignment,
      position: this.disableDragging ? "initial" : "absolute"
    };
  }
  getWrapperStyle(){
    return     (() => {
      switch (this.tabAlignment) {
        case TabAlignment.top:
          return {
           display:"flex",
           justifyContent:"flex-start",
           flexDirection:"column"
          };
        default:
          return {
            display:"flex",
            justifyContent:"flex-start",
            flexDirection:"column-reverse"
           };
      }
    })();
  }
  getTabsPlacement(){
    return {
      display:"flex",
      flexDirection: "row"
    }
  }
}
