import { CometChatThemeService } from "./../../../../CometChatTheme.service";
import { CometChat, CometChatException } from "@cometchat/chat-sdk-javascript";
import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  Input,
  TemplateRef,
  ViewChild,
  ChangeDetectorRef,
  ElementRef,
  SimpleChanges,
} from "@angular/core";
import {
  ListItemStyle,
  AvatarStyle,
  MessageInputStyle,
  DateStyle,
} from "@cometchat/uikit-elements";
import {
  CometChatOption,
  localize,
  SelectionMode,
  TitleAlignment,
  States,
  CometChatMessageTemplate,
  MessageListAlignment,
  MessageBubbleAlignment,
  CometChatMessageOption,
  CometChatUIKitConstants,
  CometChatUIEvents,
  CometChatGroupEvents,
  fontHelper,
  DatePatterns,
  Receipts,
} from "@cometchat/uikit-resources";
import {
  UsersStyle,
  BaseStyle,
  ListStyle,
  CometChatUIKitUtility,
} from "@cometchat/uikit-shared";
import { Subscription } from "rxjs";

@Component({
  selector: "aiassist-bot-message-list",
  templateUrl: "./aiassist-bot-message-list.component.html",
  styleUrls: ["./aiassist-bot-message-list.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AIAssistBotMessageListComponent implements OnInit {
  messagesList: CometChat.BaseMessage[] = [];
  botMessagesList: CometChat.BaseMessage[] = [];
  @Input() hideSearch: boolean = true;
  @Input() footerView!: TemplateRef<any>;

  @Input() user!: CometChat.User;
  @Input() group!: CometChat.Group;
  @Input() currentAskAIBot!: any;
  @Input() subtitleText: string = localize("COMETCHAT_ASK_BOT_SUBTITLE");
  @Input() sendIconUrl: string = "assets/Send.svg";
  @Input() waitIcon: string = "assets/wait.svg";
  @Input() errorIcon: string = "assets/warning-small.svg";
  @Input() botFirstMessageText: string = localize(
    "COMETCHAT_BOT_FIRST_MESSAGE"
  );
  @Input() closeButtonIconURL: string = "assets/close2x.svg";
  @Input() sendButtonIconURL: string = "assets/Send.svg";
  @Input() avatarStyle: AvatarStyle = {
    borderRadius: "16px",
    width: "28px",
    height: "28px",
  };
  @Input() aiBotChatHeaderStyle: any = {
    backGround: "rgba(20, 20, 20, 0.15)",
  };
  @Input() aiBotChatContainerStyle: any = {
    backGround: "rgba(255,255,255)",
  };
  @Input() datePattern: DatePatterns = DatePatterns.time;
  bubbleDateStyle: DateStyle = {};

  @ViewChild("inputRef", { static: false }) inputRef!: ElementRef;

  ccChatChanged!: Subscription;

  messageTemplate: CometChatMessageTemplate[] = [];
  @Input() alignment: MessageListAlignment = MessageListAlignment.standard;
  public loggedInUser!: CometChat.User;

  public receipts: any = Receipts.wait;
  public currentMessageObject: CometChat.BaseMessage | null = null;
  public typesMap: any = {};
  public messageTypesMap: any = {};
  inputValue!: string;

  constructor(
    private ref: ChangeDetectorRef,
    private themeService: CometChatThemeService
  ) {}

  ngOnInit(): void {
   
    this.setAvatarStyle();
    this.setAiBotChatHeader();
    this.setAiBotChatContainerStyle();
    let receiverId: string = this.user
      ? this.user?.getUid()!
      : this.group?.getGuid()!;

    let receiverType = this.user
      ? CometChatUIKitConstants.MessageReceiverType.user
      : CometChatUIKitConstants.MessageReceiverType.group;
   
    let firstMessage = new CometChat.TextMessage(
      this.currentAskAIBot?.id!,
      this.botFirstMessageText,
      receiverType
    );
    firstMessage.setSentAt(CometChatUIKitUtility.getUnixTimestamp());

    this.botMessagesList.push(firstMessage);

    CometChat.getLoggedinUser()
      .then((user: CometChat.User | null) => {
        this.loggedInUser = user!;
      })
      .catch((error: CometChat.CometChatException) => {
        if (this.onError) {
          this.onError(error);
        }
      });

    this.setMessagesStyle();
    this.subscribeToEvents();
  }

  setAvatarStyle() {
    let defaultStyle: AvatarStyle = new AvatarStyle({
      borderRadius: "24px",
      width: "28px",
      height: "28px",
      border: `1px solid ${this.themeService.theme.palette.getAccent200()}`,
      backgroundColor: this.themeService.theme.palette.getAccent700(),
      nameTextColor: this.themeService.theme.palette.getAccent900(),
      backgroundSize: "cover",
      nameTextFont: fontHelper(this.themeService.theme.typography.subtitle1),

      outerViewBorderSpacing: "",
    });
    this.avatarStyle = { ...defaultStyle, ...this.avatarStyle };
  }

  setAiBotChatHeader() {
    let defaultStyle: any = {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      padding: "10px",
      height: "40px",
      background: this.themeService.theme?.palette.getAccent200(),
    };
    this.aiBotChatHeaderStyle = {
      ...defaultStyle,
      ...this.aiBotChatHeaderStyle,
    };
  }
  setAiBotChatContainerStyle() {
    let defaultStyle: any = {
      background: this.themeService.theme.palette.getBackground(),
    };
    this.aiBotChatContainerStyle = {
      ...defaultStyle,
      ...this.aiBotChatContainerStyle,
    };
  }

  ngOnDestroy(): void {
    this.unSubscribeToEvent();
  }

  subscribeToEvents() {
    this.ccChatChanged = CometChatUIEvents.ccActiveChatChanged.subscribe(() => {
      CometChatUIEvents.ccShowPanel.next({
        child: { showBotView: false },
      });
    });
  }

  unSubscribeToEvent() {
    this.ccChatChanged?.unsubscribe();
  }

  onError(error: CometChatException) {
    throw new Error("Method not implemented.");
  }

  generateRandomString(length: any) {
    const characters =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    let randomString = "";

    for (let i = 0; i < length; i++) {
      const randomIndex = Math.floor(Math.random() * characters.length);
      randomString += characters.charAt(randomIndex);
    }

    return randomString;
  }

  handleClick() {
    this.receipts = Receipts.wait;

    let receiverId: string = this.user
      ? this.user?.getUid()!
      : this.group?.getGuid()!;

    let receiverType = this.user
      ? CometChatUIKitConstants.MessageReceiverType.user
      : CometChatUIKitConstants.MessageReceiverType.group;

    let botUid = this.currentAskAIBot?.id!;
    let question = this.inputValue;
    this.inputRef?.nativeElement?.emptyInputField();
    let query = new CometChat.TextMessage(
      this.loggedInUser.getUid()!,
      question,
      receiverType
    );

    // Generate a random string for the message ID
    let randomString = this.generateRandomString(10); // Specify the desired length

    // Convert the random string to a number (may not be the best practice)
    let randomStringAsNumber = parseInt(randomString, 36); // 36 is the radix for alphanumeric characters

    query.setId(randomStringAsNumber);

    query.setSentAt(CometChatUIKitUtility.getUnixTimestamp());
    this.currentMessageObject = query;
    if (question.trim().length > 0) {
      this.botMessagesList.push(query);

      CometChat.askBot(receiverId, receiverType, botUid, question)
        .then((response) => {
          return response;
        })
        .then((messageText) => {
          this.inputValue = "";
          this.receipts = null;
          let answer = new CometChat.TextMessage(
            botUid,
            messageText,
            receiverType
          );
          answer.setSentAt(CometChatUIKitUtility.getUnixTimestamp());

          this.botMessagesList.push(answer);
          this.ref.detectChanges();
        })
        .catch((error) => {
          this.receipts = Receipts.error;

          console.error("error", error);
        });
    }
  }

  setMessageBubbleStyle(msg: CometChat.BaseMessage): BaseStyle {
    let style!: BaseStyle;
    if (msg.getReceiverId() !== this.loggedInUser?.getUid()) {
      style = {
        background: this.themeService.theme.palette.getAccent100(),
        borderRadius: "12px",
      };
    } else {
      style = {
        background: this.themeService.theme.palette.getPrimary(),
        borderRadius: "12px",
      };
    }

    return style;
  }

  getBubbleAlignment(message: CometChat.BaseMessage) {
    return message.getReceiverId() !== this.loggedInUser?.getUid()
      ? MessageBubbleAlignment.left
      : MessageBubbleAlignment.right;
  }

  messageInputStyle: MessageInputStyle = {
    width: "98%",
    dividerColor: "transparent",
    background: "transparent",
    border: "2px solid red transparent",
  };

  sendButtonStyle: any = {
    height: "24px",
    width: "24px",
    border: "none",
    borderRadius: "0",
    background: "transparent",
    buttonIconTint: this.themeService.theme.palette.getPrimary(),
  };

  messageInputChanged = (event: any) => {
    const text = event?.detail?.value?.trim();
    this.inputValue = text;
  };

  closeButtonStyle: any = {
    height: "24px",
    width: "24px",
    border: "none",
    borderRadius: "0",
    background: "transparent",
    buttonIconTint: this.themeService.theme.palette.getPrimary(),
  };

  labelStyle: any = {
    textFont: "400 11px Inter",
    textColor: "grey",
  };

  onCloseDetails() {
    CometChatUIEvents.ccShowPanel.next({
      child: { showBotView: false },
    });
  }

  setMessagesStyle() {
    this.bubbleDateStyle = {
      textColor: this.themeService.theme.palette.getAccent600(),
      textFont: fontHelper(this.themeService.theme.typography.caption2),
    };
  }

   getBotTitleStyle(){
    return {
        font:  fontHelper(this.themeService.theme.typography.subtitle1),
        color: this.themeService.theme.palette.getAccent(),
    }
}

 getBotSubtitleStyle(){
    return {
        font: fontHelper(this.themeService.theme.typography.caption2),
        color:this.themeService.theme.palette.getAccent500(),
    }
}

}
