import {
  Component,
  OnInit,
  ChangeDetectorRef,
  Input,
  ChangeDetectionStrategy,
  TemplateRef,
} from "@angular/core";

import { CometChat } from "@cometchat/chat-sdk-javascript";
import { ListStyle, BaseStyle } from "@cometchat/uikit-shared";
import {
  AvatarStyle,
  DateStyle,
  IconStyle,
  ListItemStyle,
} from "@cometchat/uikit-elements";
import { CometChatThemeService } from "../../../CometChatTheme.service";
import {
  CometChatOption,
  localize,
  fontHelper,
  DatePatterns,
  SelectionMode,
  States,
} from "@cometchat/uikit-resources";
import {
  CallLogRecordingsStyle,
  CallingDetailsUtils,
} from "@cometchat/uikit-shared";
import { CallLogUtils } from "../../../Shared/Utils/CallLogUtils";
@Component({
  selector: "cometchat-call-log-recordings",
  templateUrl: "./cometchat-call-log-recordings.component.html",
  styleUrls: ["./cometchat-call-log-recordings.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CometChatCallLogRecordingsComponent implements OnInit {
  @Input() title: string = localize("RECORDING");
  @Input() call!: any;
  @Input() onBackClick!: () => void;
  @Input() backIconURL: string = "assets/backbutton.svg";
  @Input() datePattern: DatePatterns = DatePatterns.DayDateTime;
  @Input() subtitleView!: any;
  @Input() listItemView!: any;
  @Input() downloadIconURL: string = "assets/download.svg";
  @Input() onDownloadClick!: (item: any) => void;

  @Input() hideDownloadButton: boolean = false;

  @Input() onError: (error: CometChat.CometChatException) => void = (
    error: CometChat.CometChatException
  ) => {
    console.log(error);
  };

  @Input() avatarStyle: AvatarStyle = {
    borderRadius: "16px",
    width: "32px",
    height: "32px",
  };
  @Input() dateStyle: DateStyle = {};
  @Input() CallLogRecordingsStyle: CallLogRecordingsStyle = {
    width: "100%",
    height: "100%",
  };
  @Input() listItemStyle: ListItemStyle = {};
  public recordingsList: any = [];
  public loggedInUser: CometChat.User | null = null;

  public state: States = States.loading;
  listStyle: ListStyle = {};
  public limit: number = 30;
  @Input() options!: ((member: CometChat.Call) => CometChatOption[]) | null;
  iconStyle: IconStyle = {
    height: "16px",
    width: "16px",
    iconTint: "RGBA(20, 20, 20, 0.68)",
  };

  constructor(
    private ref: ChangeDetectorRef,
    private themeService: CometChatThemeService
  ) {
    this.state = States.loading;
  }
  ngOnInit(): void {
    this.setThemeStyle();
   
    CometChat.getLoggedInUser()
      .then((user: CometChat.User | null) => {
        this.loggedInUser = user;
        this.recordingsList = this.call?.getRecordings();
        this.state = States.loaded;

        this.ref.detectChanges()
      })
      .catch((error: CometChat.CometChatException) => {
        if (this.onError) {
          this.onError(error);
        }
      });
  }

  handleDownloadClick(item: any) {
    if (this.onDownloadClick) {
      this.onDownloadClick(item);
    } else {
      this.download(item?.getRecordingURL());
    }
  }

  download = async (url: any) => {
    try {
      const response = await fetch(url);
      if (!response.ok) {
        throw new Error(
          `Failed to download file (HTTP status: ${response.status})`
        );
      }
      const blob = await response.blob();

      // Create a temporary link element
      const link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      link.download = "file.mp4"; // Set the desired file name here
      link.style.display = "none";

      // Append the link to the body and trigger the download
      document.body.appendChild(link);
      link.click();

      // Clean up
      document.body.removeChild(link);
    } catch (error) {
      console.error("Error downloading file:", error);
    }
  };

  getSubtitle(totalSeconds: number) {
    return CallLogUtils.convertMinutesToHoursMinutesSeconds(totalSeconds);
  }

  callStyle = () => {
    return {
      height: this.CallLogRecordingsStyle.height,
      width: this.CallLogRecordingsStyle.width,
      background: this.CallLogRecordingsStyle.background,
      border: this.CallLogRecordingsStyle.border,
      borderRadius: this.CallLogRecordingsStyle.borderRadius,
    };
  };
  setThemeStyle() {
    this.iconStyle.iconTint = this.themeService.theme.palette.getAccent600();
    this.setAvatarStyle();
    this.setDateStyle();
    this.setCallRecordingsStyle();
    this.listStyle = {
      titleTextFont: this.CallLogRecordingsStyle.titleFont,
      titleTextColor: this.CallLogRecordingsStyle.titleColor,
    };
  }
  setAvatarStyle() {
    let defaultStyle: AvatarStyle = new AvatarStyle({
      borderRadius: "24px",
      width: "36px",
      height: "36px",
      border: "none",
      backgroundColor: this.themeService.theme.palette.getAccent700(),
      nameTextColor: this.themeService.theme.palette.getAccent900(),
      backgroundSize: "cover",
      nameTextFont: fontHelper(this.themeService.theme.typography.subtitle1),

      outerViewBorderSpacing: "",
    });
    this.avatarStyle = { ...defaultStyle, ...this.avatarStyle };
  }
  setDateStyle() {
    let defaultStyle: DateStyle = new DateStyle({
      textFont: fontHelper(this.themeService.theme.typography.caption2),
      textColor: this.themeService.theme.palette.getAccent600(),
      background: "transparent",
    });
    this.dateStyle = { ...defaultStyle, ...this.dateStyle };
  }

  getListItemStyle() {
    let defaultStyle: ListItemStyle = new ListItemStyle({
      height: "45px",
      width: "100%",
      background: this.themeService.theme.palette.getBackground(),
      activeBackground: this.themeService.theme.palette.getAccent100(),
      borderRadius: "0",
      titleFont: fontHelper(this.themeService.theme.typography.title2),
      border: "none",
      separatorColor:
        "rgb(222 222 222 / 46%)" ||
        this.themeService.theme.palette.getAccent200(),
      hoverBackground: this.themeService.theme.palette.getAccent50(),
      padding: "0",
    });
    return { ...defaultStyle, ...this.listItemStyle };
  }
  setCallRecordingsStyle() {
    let defaultStyle: CallLogRecordingsStyle = new CallLogRecordingsStyle({
      recordingDurationFont: fontHelper(
        this.themeService.theme.typography.subtitle2
      ),
      recordingDurationColor: this.themeService.theme.palette.getAccent600(),
      background: this.themeService.theme.palette.getBackground(),
      border: `1px solid ${this.themeService.theme.palette.getAccent50()}`,
      titleFont: fontHelper(this.themeService.theme.typography.title1),
      titleColor: this.themeService.theme.palette.getAccent(),

      backIconTint: this.themeService.theme.palette.getPrimary(),
      dateTextFont: fontHelper(this.themeService.theme.typography.subtitle2),
      dateTextColor: this.themeService.theme.palette.getAccent600(),
    });
    this.CallLogRecordingsStyle = {
      ...this.CallLogRecordingsStyle,
      ...defaultStyle,
    };
  }
  subtitleStyle = () => {
    return {
      font: this.CallLogRecordingsStyle.recordingDurationFont,
      color: this.CallLogRecordingsStyle.recordingDurationColor,
    };
  };

  backButtonStyle = () => {
    return {
      height: "24px",
      width: "24px",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      buttonIconTint: this.CallLogRecordingsStyle?.backIconTint,
    };
  };
  downLoadIconStyle = () => {
    return {
      height: "24px",
      width: "24px",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      buttonIconTint: this.CallLogRecordingsStyle?.backIconTint,
    };
  };

  handleBackClick = () => {
    if (this.onBackClick) {
      this.onBackClick();
      this.ref.detectChanges();
    }
  };
  titleStyle = () => {
    return {
      font: this.CallLogRecordingsStyle.titleFont,
      color: this.CallLogRecordingsStyle.titleColor,
      background: "transparent",
    };
  };
}
