import { CometChatCallButtons } from "./../../Calls/CometChatCallButtons/cometchat-call-buttons.module";
import {
  CometChatCallDetailsOption,
  CometChatCallDetailsTemplate,
  CometChatDetailsTemplate,
  CometChatTheme,
  CometChatUIKitConstants,
  DatePatterns,
  fontHelper,
  localize,
} from "@cometchat/uikit-resources";
import { DateStyle, ListItemStyle } from "@cometchat/uikit-elements";
import { DataSource } from "../Framework/DataSource";

export class CallDetailUtils {
  public static getDefaultCallTemplate(
    callLog: any,
    loggedInUser: CometChat.User,
    theme: CometChatTheme
  ): Array<CometChatCallDetailsTemplate> {
    return [
      this.getPrimaryDetailsTemplate(callLog, loggedInUser, theme),
      this.getSecondaryDetailsTemplate(callLog, loggedInUser, theme),
    ];
  }

  public static getPrimaryDetailsTemplate(
    callLog: any,
    loggedInUser: CometChat.User,
    theme: CometChatTheme
  ): CometChatCallDetailsTemplate {
    const template: CometChatCallDetailsTemplate =
      new CometChatCallDetailsTemplate({
        id: "callControls",
        hideSectionSeparator: true,
        sectionSeparatorColor: "red",
        options: (
          user: CometChat.User | null,
          group: CometChat.Group | null
        ) => {
          return this.getPrimaryOptions(
            user ?? undefined,
            group ?? undefined,
            callLog ?? undefined,
            loggedInUser ?? undefined,
            theme ?? undefined
          );
        },
      });
    return template;
  }

  private static getPrimaryOptions(
    user?: CometChat.User,
    group?: CometChat.Group,
    callLog?: any,
    loggedInUser?: CometChat.User,
    theme?: CometChatTheme
  ): CometChatCallDetailsOption[] {
    const options: CometChatCallDetailsOption[] = [];
    if (user) {
      options.push(
        new CometChatCallDetailsOption({
          id: "calls",
        })
      );
    }
    options.push(
      new CometChatCallDetailsOption({
        id: "callStatus",
        title: callLog?.getInitiatedAt()!,
        titleFont: fontHelper(theme!.typography.subtitle1),
        backgroundColor: "rgba(20, 20, 20, 0.04)",
      })
    );
    return options;
  }

  public static getSecondaryDetailsTemplate(
    callLog: any,
    loggedInUser: CometChat.User,
    theme: CometChatTheme
  ): CometChatCallDetailsTemplate {
    const template: CometChatCallDetailsTemplate =
      new CometChatCallDetailsTemplate({
        id: "callOptions",
        hideSectionSeparator: true,
        options: () => {
          return this.getSecondaryOptions(
            callLog ?? undefined,
            theme ?? undefined
          );
        },
      });
    return template;
  }

  private static getSecondaryOptions(
    callLog?: any,
    theme?: CometChatTheme
  ): CometChatCallDetailsOption[] {
    const options: CometChatCallDetailsOption[] = [];
    const localizedParticipants = "Participants";
    const localizedRecording = "Recordings";
    const localizedHistory = "History";

    options.push(
      new CometChatCallDetailsOption({
        id: "participants",
        title: localizedParticipants,
        tail: callLog.participants.length,
        backgroundColor: "rgba(20, 20, 20, 0.04)",
      })
    );
    if (callLog?.hasRecording) {
      options.push(
        new CometChatCallDetailsOption({
          id: "recordings",
          title: localizedRecording,
          tail: callLog.recordings.length,
          backgroundColor: "rgba(20, 20, 20, 0.04)",
        })
      );
    }

    options.push(
      new CometChatCallDetailsOption({
        id: "callHistory",
        title: localizedHistory,
        backgroundColor: "rgba(20, 20, 20, 0.04)",
      })
    );

    return options;
  }
}
