import { CometChat } from "@cometchat/chat-sdk-javascript";
import { AIExtensionDataSource } from "../../Shared/Framework/AIExtensionDataSource";
import { ChatConfigurator } from "../../Shared/Framework/ChatConfigurator";
import { AISmartRepliesExtensionDecorator } from "./AISmartRepliesDecorator";
export class AISmartRepliesExtension extends AIExtensionDataSource {
  constructor() {
    super();
  }
  override addExtension(): void {
    ChatConfigurator.enable(
      (dataSource: any) => new AISmartRepliesExtensionDecorator(dataSource)
    );
  }
  override getExtensionId(): string {
    return "smart-replies";
  }

  override async enable(): Promise<void> {
    if (await CometChat.isAIFeatureEnabled(this.getExtensionId())) {
      this.addExtension();
    }
  }
}
