import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges,
  TemplateRef,
} from "@angular/core";
import { CometChat } from "@cometchat/chat-sdk-javascript";

import { CometChatUIKitCalls, ListStyle, OutgoingCallStyle } from "@cometchat/uikit-shared";
import {
  AvatarStyle,
  DateStyle,
  IconStyle,
  ListItemStyle,
} from "@cometchat/uikit-elements";
import { Subscription } from "rxjs";
import { CometChatThemeService } from "../../../CometChatTheme.service";
import {
  localize,
  CometChatUIKitConstants,
  fontHelper,
  DatePatterns,
  States,
} from "@cometchat/uikit-resources";
import { CallLogHistoryStyle } from "@cometchat/uikit-shared";
import { CometChatException } from "../../../Shared/Utils/ComeChatException";
import { CallLogUtils } from "../../../Shared/Utils/CallLogUtils";

@Component({
  selector: "cometchat-call-log-history",
  templateUrl: "./cometchat-call-log-history.component.html",
  styleUrls: ["./cometchat-call-log-history.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CometChatCallLogHistoryComponent implements OnInit {
  @Input() user!: CometChat.User;
  @Input() group!: CometChat.Group;
  @Input() title: string = localize("CALL_HISTORY");
  @Input() emptyStateView!: TemplateRef<any>;
  @Input() errorStateView!: TemplateRef<any>;
  @Input() loadingStateView!: TemplateRef<any>;
  @Input() subtitleView!: TemplateRef<any>;
  @Input() listItemView!: TemplateRef<any>;
  @Input() menu!: TemplateRef<any>;

  @Input() emptyStateText: string = localize("NO_CALLS_FOUND");
  @Input() errorStateText: string = localize("SOMETHING_WRONG");
  @Input() loadingIconURL: string = "assets/Spinner.svg";
  @Input() backIconUrl: string = "assets/backbutton.svg";

  @Input() onItemClick!: (call: any) => void; 
  @Input() onBackClick!: () => void;

  @Input() callLogRequestBuilder!: any;

  @Input() DateSeparatorPattern: DatePatterns = DatePatterns.time;

  @Input() hideSeparator: boolean = false;

  @Input() dateSeparatorStyle: DateStyle = {
    height: "",
    width: "",
  };

  @Input() hideError: boolean = false;

  @Input() onError: (error: CometChat.CometChatException) => void = (
    error: CometChat.CometChatException
  ) => {
    console.log(error);
  };
  @Input() showSectionHeader: boolean = true;
  @Input() sectionHeaderField: any = "initiatedAt";
  @Input() datePattern: DatePatterns = DatePatterns.DayDateTime;
  @Input() avatarStyle: AvatarStyle = {
    borderRadius: "16px",
    width: "32px",
    height: "32px",
  };
  @Input() dateStyle: DateStyle = {};
  @Input() CallLogHistoryStyle: CallLogHistoryStyle = {
    width: "100%",
    height: "100%",
  };

  @Input() listItemStyle: ListItemStyle = {};

  callsRequest!: any;
  public state: States = States.loading;
  ccOutgoingCall!: Subscription;
  ccCallAccepted!: Subscription;
  ccCallRejected!: Subscription;
  ccCallEnded!: Subscription;
  listStyle: ListStyle = {};
  public limit: number = 30;
  public callHistory: any[] = [];
  public callsListenerId: string = "callsList_" + new Date().getTime();
  public loggedInUser: CometChat.User | null = null;
  public authToken: string = "";
  showOutgoingCallscreen: boolean = false;
  onScrolledToBottom: any = null;

  constructor(
    private ref: ChangeDetectorRef,
    private themeService: CometChatThemeService
  ) {
    this.state = States.loading;
  }
  ngOnInit(): void {
    this.setThemeStyle();

    CometChat.getLoggedinUser()
      .then((user: CometChat.User | null) => {
        this.loggedInUser = user;
        this.authToken = this.loggedInUser!.getAuthToken();
        this.callsRequest = this.getRequestBuilder?.();
        this.fetchNextCallHistoryList();
      })
      .catch((error: CometChat.CometChatException) => {
        if (this.onError) {
          this.onError(error);
        }
      });
    this.state = States.loading;
  }

  handleItemClick(call: any) {
    if (this.onItemClick) {
      this.onItemClick(call);
    }
  }

  ngOnDestroy() {
    this.callsRequest = null;
    this.ref.detach();
  }

  fetchNextCallHistoryList = () => {
    this.onScrolledToBottom = null;
    this.state = States.loading;
    this.ref.detectChanges();
    try {
      this.callsRequest!.fetchNext()
        .then(
          (callHistory: any) => {
            if (callHistory?.length > 0) {
              this.onScrolledToBottom = this.fetchNextCallHistoryList;
              this.ref.detectChanges();
            }
            if (
              (callHistory.length <= 0 && this.callHistory?.length <= 0) ||
              (callHistory.length === 0 && this.callHistory?.length <= 0)
            ) {
              this.state = States.empty;
              this.ref.detectChanges();
            } else {
              this.state = States.loaded;

              this.callHistory = [...this.callHistory, ...callHistory];

              this.ref.detectChanges();
            }
          },
          (error: any) => {
            if (this.onError) {
              this.onError(CometChatException(error));
            }
            this.state = States.error;
            this.ref.detectChanges();
          }
        )
        .catch((error: CometChat.CometChatException) => {
          if (this.onError) {
            this.onError(error);
          }
        });
    } catch (error: any) {
      this.state = States.error;
      this.ref.detectChanges();
      if (this.onError) {
        this.onError(CometChatException(error));
      }
    }
  };

  getSubtitle(call: any): string {
    return CallLogUtils.getCallStatusWithType(call, this.loggedInUser!);
  }

  getRequestBuilder() {
    if (this.callLogRequestBuilder) {
      return this.callLogRequestBuilder?.build();
    } else {
      if (this.user) {
        return new CometChatUIKitCalls.CallLogRequestBuilder()
          .setLimit(this.limit)
          .setCallCategory("call")
          .setAuthToken(this.authToken)
          .setUid(this.user.getUid())
          .build();
      } else if (this.group) {
        return new CometChatUIKitCalls.CallLogRequestBuilder()
          .setLimit(this.limit)
          .setCallCategory("call")
          .setAuthToken(this.authToken)
          .setGuid(this.group.getGuid())
          .build();
      } else {
        return null;
      }
    }
  }

  callLogHistoryStyle = () => {
    return {
      height: this.CallLogHistoryStyle.height,
      width: this.CallLogHistoryStyle.width,
      background: this.CallLogHistoryStyle.background,
      border: this.CallLogHistoryStyle.border,
      borderRadius: this.CallLogHistoryStyle.borderRadius,
    };
  };

  setThemeStyle() {
    this.setAvatarStyle();
    this.setDateStyle();
    this.setCallLogHistoryStyle();
    this.listStyle = {
      titleTextFont: this.CallLogHistoryStyle.titleFont,
      titleTextColor: this.CallLogHistoryStyle.titleColor,
      emptyStateTextFont: this.CallLogHistoryStyle.emptyStateTextFont,
      emptyStateTextColor: this.CallLogHistoryStyle.emptyStateTextColor,
      errorStateTextFont: this.CallLogHistoryStyle.errorStateTextFont,
      errorStateTextColor: this.CallLogHistoryStyle.errorStateTextColor,
      loadingIconTint: this.CallLogHistoryStyle.loadingIconTint,
      separatorColor: this.CallLogHistoryStyle.dateSeparatorTextColor,
      sectionHeaderTextColor: this.CallLogHistoryStyle.dateSeparatorTextColor,
      sectionHeaderTextFont: this.CallLogHistoryStyle.dateSeparatorTextFont,
    };
  }
  setAvatarStyle() {
    let defaultStyle: AvatarStyle = new AvatarStyle({
      borderRadius: "24px",
      width: "36px",
      height: "36px",
      border: "none",
      backgroundColor: this.themeService.theme.palette.getAccent700(),
      nameTextColor: this.themeService.theme.palette.getAccent900(),
      backgroundSize: "cover",
      nameTextFont: fontHelper(this.themeService.theme.typography.subtitle1),

      outerViewBorderSpacing: "",
    });
    this.avatarStyle = { ...defaultStyle, ...this.avatarStyle };
  }
  setDateStyle() {
    let defaultStyle: DateStyle = new DateStyle({
      textFont: fontHelper(this.themeService.theme.typography.subtitle2),
      textColor: this.themeService.theme.palette.getAccent600(),
      background: "transparent",
    });
    this.dateStyle = { ...defaultStyle, ...this.dateStyle };
  }

  getListItemStyle() {
    let defaultStyle: ListItemStyle = new ListItemStyle({
      height: "45px",
      width: "100%",
      background: this.themeService.theme.palette.getBackground(),
      activeBackground: this.themeService.theme.palette.getAccent100(),
      borderRadius: "0",
      titleFont: fontHelper(this.themeService.theme.typography.title2),
      border: "none",
      separatorColor:
        "rgb(222 222 222 / 46%)" ||
        this.themeService.theme.palette.getAccent200(),
      hoverBackground: this.themeService.theme.palette.getAccent50(),
      padding: "0",
    });
    return { ...defaultStyle, ...this.listItemStyle };
  }

  setCallLogHistoryStyle() {
    let defaultStyle: CallLogHistoryStyle = new CallLogHistoryStyle({
      background: this.themeService.theme.palette.getBackground(),
      border: `1px solid ${this.themeService.theme.palette.getAccent50()}`,
      titleFont: fontHelper(this.themeService.theme.typography.title1),
      titleColor: this.themeService.theme.palette.getAccent(),
      emptyStateTextFont: fontHelper(this.themeService.theme.typography.title1),
      emptyStateTextColor: this.themeService.theme.palette.getAccent600(),
      errorStateTextFont: fontHelper(this.themeService.theme.typography.title1),
      errorStateTextColor: this.themeService.theme.palette.getAccent600(),
      loadingIconTint: this.themeService.theme.palette.getAccent600(),
      backIconTint: this.themeService.theme.palette.getPrimary(),
      dateTextFont: fontHelper(this.themeService.theme.typography.subtitle2),
      dateSeparatorTextFont: fontHelper(
        this.themeService.theme.typography.subtitle2
      ),
      dateSeparatorTextColor: this.themeService.theme.palette.getAccent400(),
      callDurationTextFont: fontHelper(
        this.themeService.theme.typography.caption1
      ),
      callDurationTextColor: this.themeService.theme.palette.getAccent500(),
      callStatusTextFont: fontHelper(
        this.themeService.theme.typography.subtitle2
      ),
      callStatusTextColor: this.themeService.theme.palette.getAccent400(),
      dividerColor: this.themeService.theme.palette.getAccent600(),
    });

    this.CallLogHistoryStyle = { ...defaultStyle, ...this.CallLogHistoryStyle };
  }
  subtitleStyle = () => {
    return {
      font: this.CallLogHistoryStyle.dateTextColor,
      color: this.CallLogHistoryStyle.dateTextFont,
    };
  };
  tailViewStyle = () => {
    return {
      font: this.CallLogHistoryStyle.callDurationTextFont,
      color: this.CallLogHistoryStyle.callDurationTextColor,
    };
  };

  backButtonStyle = () => {
    return {
      height: "24px",
      width: "24px",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      buttonIconTint:
        this.CallLogHistoryStyle.backIconTint ||
        this.themeService.theme.palette.getPrimary(),
    };
  };

 
  getSectionHeader = (call: any, index: any) => {
    if (this.callHistory && this.callHistory.length > 0 && index === 0) {
      return this.callHistory[0]["initiatedAt"];
    }

    if (
      this.callHistory &&
      index > 0 &&
      CallLogUtils.isDateDifferent(
        this.callHistory[index - 1]["initiatedAt"],
        this.callHistory[index]["initiatedAt"]
      )
    ) {
      return call.initiatedAt;
    }
  };

  getTailView(totalSeconds: any) {
    return CallLogUtils.convertMinutesToHoursMinutesSeconds(totalSeconds);
  }

  handleBackClick = () => {
    if (this.onBackClick) {
      this.onBackClick();
      this.ref.detectChanges();
    }
  };

  titleStyle = () => {
    return {
      font: this.CallLogHistoryStyle.titleFont,
      color: this.CallLogHistoryStyle.titleColor,
      background: "transparent",
    };
  };
}
