import { CometChat } from "@cometchat/chat-sdk-javascript";
import {
  AIConversationStarterConfiguration,
  AIOptionsStyle,
} from "@cometchat/uikit-shared";
import { DataSourceDecorator } from "../../Shared/Framework/DataSourceDecorator";
import { DataSource } from "../../Shared/Framework/DataSource";
import {
  CometChatActionsView,
  CometChatMessageComposerAction,
  CometChatTheme,
  fontHelper,
  localize,
} from "@cometchat/uikit-resources";

export class AIAssistBotDecorator extends DataSourceDecorator {
  public aiBotsList: any = [];
  constructor(dataSource: DataSource) {
    super(dataSource);
    this.fetchAiBots();
  }

  override getId() {
    return "aiassistbot";
  }
  override getAIOptions(
    theme: CometChatTheme,
    id?: any,
    aiOptionsStyles?: AIOptionsStyle
  ): (CometChatMessageComposerAction | CometChatActionsView)[] {
    let title;
    if (this.aiBotsList && this.aiBotsList.length == 1) {
      title = `${localize("COMETCHAT_ASK_BOT")} ${this.aiBotsList[0]?.name}`;
    } else {
      title = localize("COMETCHAT_ASK_AI_BOT");
    }
    if (!id?.parentMessageId && this.aiBotsList && this.aiBotsList.length > 0) {
      const messageComposerActions: (
        | CometChatMessageComposerAction
        | CometChatActionsView
      )[] = super.getAIOptions(theme, id, aiOptionsStyles);
      let newAction: CometChatMessageComposerAction =
        new CometChatMessageComposerAction({
          title: title,
          id: "ai-bots",
          onClick: () => {
            return this.aiBotsList;
          },
          iconURL: "",
          iconTint: "",
          titleColor:
            aiOptionsStyles?.listItemTextColor || theme.palette.getAccent600(),
          titleFont:
            aiOptionsStyles?.listItemTextFont ||
            fontHelper(theme.typography.subtitle1),
          background:
            aiOptionsStyles?.listItemBackground || theme.palette.getAccent100(),
        });
      messageComposerActions.push(newAction);
      return messageComposerActions;
    } else {
      return super.getAIOptions(theme, id, aiOptionsStyles);
    }
  }

  async fetchAiBots() {
    try {
      let tags = ["aibot"];

      let botsRequest = new CometChat.UsersRequestBuilder()
        .setLimit(30)
        .setTags(tags)
        .build();

      const botList = await botsRequest.fetchNext();

      botList.forEach((e: any) => {
        let newAction: CometChatMessageComposerAction =
          new CometChatMessageComposerAction({
            id: e.uid,
            title: e.name,
            iconURL: e.avatar,
            onClick: () => {
              return e;
            },
          });
        this.aiBotsList.push(newAction);
      });
    } catch (error) {
      console.error("An error occurred while fetching AI bots:", error);
    }
  }
}
