import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges,
  TemplateRef,
} from "@angular/core";
import { CometChat } from "@cometchat/chat-sdk-javascript";

import {
  ListStyle,
  OutgoingCallConfiguration,
  CometChatSoundManager,
  OutgoingCallStyle,
  CometChatUIKitCalls,
  CallScreenConfiguration,
  CallLogsStyle,
} from "@cometchat/uikit-shared";
import {
  AvatarStyle,
  BackdropStyle,
  CallscreenStyle,
  DateStyle,
  ListItemStyle,
} from "@cometchat/uikit-elements";
import { CometChatThemeService } from "../../../CometChatTheme.service";
import {
  localize,
  CometChatUIKitConstants,
  fontHelper,
  DatePatterns,
  CometChatCallEvents,
  TitleAlignment,
  States,
} from "@cometchat/uikit-resources";
import { CallLogUtils } from "../../../Shared/Utils/CallLogUtils";

@Component({
  selector: "cometchat-call-logs",
  templateUrl: "./cometchat-call-logs.component.html",
  styleUrls: ["./cometchat-call-logs.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CometchatCallLogsComponent implements OnInit {
  @Input() title: string = localize("CALLS");
  @Input() titleAlignment: TitleAlignment = TitleAlignment.left;
  @Input() listItemView!: any;
  @Input() subtitleView!: any;
  @Input() tailView!: any;
  @Input() menu!: TemplateRef<any>;
  @Input() emptyStateView!: TemplateRef<any>;
  @Input() errorStateView!: TemplateRef<any>;
  @Input() emptyStateText: string = localize("NO_CALLS_FOUND");
  @Input() errorStateText: string = localize("SOMETHING_WRONG");
  @Input() loadingStateView!: TemplateRef<any>;

  @Input() loadingIconURL: string = "assets/Spinner.svg";
  @Input() infoIconUrl: string = "assets/InfoIcon.svg";
  @Input() missedAudioCallIconUrl: string = "assets/missedAudioCallIconUrl.svg";
  @Input() missedVideoCallIconUrl: string = "assets/missedVideoCallIconUrl.svg";
  @Input() outgoingAudioCallIconUrl: string =
    "assets/outgoingAudioCallIconUrl.svg";
  @Input() outgoingVideoCallIconUrl: string =
    "assets/outgoingVideoCallIconUrl.svg";
  @Input() incomingAudioCallIconUrl: string =
    "assets/incomingAudioCallIconUrl.svg";
  @Input() incomingVideoCallIconUrl: string =
    "assets/incomingVideoCallIconUrl.svg";

  @Input() callLogRequestBuilder!: any;
  @Input() cometchatCallObject!: CometChat.Call | null;
  @Input() onItemClick!: (call: any) => void;
  @Input() onInfoClick!: (call: any) => void;
  @Input() onError: (error: CometChat.CometChatException) => void = (
    error: CometChat.CometChatException
  ) => {
    console.log(error);
  };

  @Input() activeCall!: any;
  @Input() datePattern: DatePatterns = DatePatterns.time;
  @Input() DateSeparatorPattern: DatePatterns = DatePatterns.DayDate;

  @Input() callLogsStyle: CallLogsStyle = {
    width: "100%",
    height: "100%",
  };

  @Input() avatarStyle: AvatarStyle = {
    borderRadius: "16px",
    width: "32px",
    height: "32px",
  };
  @Input() hideSeparator: boolean = false;

  @Input() dateSeparatorStyle: DateStyle = {
    height: "",
    width: "",
  };

  @Input() outgoingCallConfiguration: OutgoingCallConfiguration =
    new OutgoingCallConfiguration({});

  @Input() hideError: boolean = false;

  @Input() showSectionHeader: boolean = true;
  @Input() showMoreInfo: boolean = false;
  @Input() sectionHeaderField: any = "initiatedAt";

  @Input() backdropStyle: BackdropStyle = {};

  @Input() dateStyle: DateStyle = {};

  @Input() listItemStyle: ListItemStyle = {};
  @Input() ongoingCallConfiguration: CallScreenConfiguration =
    new CallScreenConfiguration({});

  public selectedOrActiveCallLogObject!: any;
  callsRequest!: any;
  public state: States = States.loading;
  listStyle: ListStyle = {};

  sessionId: string = "";
  public callLogsListenerId: string = "calllogscalling" + new Date().getTime();

  ongoingCallStyle: CallscreenStyle = {};

  showOngoingCall: boolean = false;

  public limit: number = 30;
  public callsList: any = [];
  public callsListenerId: string = "callsList_" + new Date().getTime();
  public loggedInUser: CometChat.User | null = null;
  public authToken: string = "";
  showOutgoingCallscreen: boolean = false;
  onScrolledToBottom: any = null;

  outgoingCallStyle: OutgoingCallStyle = {
    width: "360px",
    height: "581px",
    titleTextFont: "700 22px Inter",
    titleTextColor: "RGB(20, 20, 20)",
    subtitleTextFont: "400 15px Inter",
    subtitleTextColor: "RGBA(20, 20, 20, 0.58)",
    borderRadius: "8px",
  };

  constructor(
    private elementRef: ElementRef,
    private ref: ChangeDetectorRef,
    private themeService: CometChatThemeService
  ) {
    this.state = States.loading;
  }

  ngOnInit(): void {
    this.setThemeStyle();
    this.attachListeners();
    CometChat.getLoggedinUser()
      .then((user: CometChat.User | null) => {
        this.loggedInUser = user;
        this.authToken = this.loggedInUser!.getAuthToken();
        this.callsRequest = this.getRequestBuilder();
        this.fetchNextCallLogsList();
      })
      .catch((error: CometChat.CometChatException) => {
        if (this.onError) {
          this.onError(error);
        }
      });
  }

  attachListeners() {
    CometChat.addCallListener(
      this.callLogsListenerId,
      new CometChat.CallListener({
        onIncomingCallReceived: (call: CometChat.Call) => {
          this.cometchatCallObject = call;
          this.ref.detectChanges();
        },
        onIncomingCallCancelled: (call: CometChat.Call) => {
          this.cometchatCallObject = null;

          this.ref.detectChanges();
        },
        onOutgoingCallRejected: (call: CometChat.Call) => {
          this.cometchatCallObject = null;
          this.showOutgoingCallscreen = false;
          this.ref.detectChanges();
        },
        onOutgoingCallAccepted: (call: CometChat.Call) => {
          this.cometchatCallObject = call;
          this.openOngoingCallScreen(call);
        },
        onCallEndedMessageReceived: (call: CometChat.Call) => {
          this.cometchatCallObject = null;
          this.ref.detectChanges();
        },
      })
    );
  }

  ngOnDestroy() {
    this.callsRequest = null;
    this.removeListener();

    this.ref.detach();
  }

  removeListener() {
    CometChat.removeCallListener(this.callLogsListenerId);
  }

  fetchNextCallLogsList = () => {
    this.onScrolledToBottom = null;
    this.state = States.loading;
    this.ref.detectChanges();
    this.callsRequest!.fetchNext()
      .then((callList: any) => {
        if (callList?.length > 0) {
          this.onScrolledToBottom = this.fetchNextCallLogsList;
          this.ref.detectChanges();
        }
        if (callList.length <= 0 && this.callsList?.length <= 0) {
          this.state = States.empty;
          this.ref.detectChanges();
        } else {
          this.state = States.loaded;

          this.callsList = [...this.callsList, ...callList];

          if (this.callsList.length < 1) {
            this.state = States.empty;
            this.ref.detectChanges();
          }
          this.ref.detectChanges();
        }
      })
      .catch((error: CometChat.CometChatException) => {
        if (this.onError) {
          this.onError(error);

          this.state = States.error;
          this.ref.detectChanges();
        }

        this.state = States.error;
        this.ref.detectChanges();
      });
  };

  getSubtitle(call: any): any {
    return CallLogUtils.getCallStatusWithType(call, this.loggedInUser!);
  }
  handleInfoClick = (call: any) => {
    this.selectedOrActiveCallLogObject = call;
    this.ref.detectChanges();

    if (this.onInfoClick) {
      this.onInfoClick(call);
    }
  };

  getActiveCall = (call: any) => {
    if (
      call.getSessionID() !== undefined &&
      call.getSessionID() === this.selectedOrActiveCallLogObject?.getSessionID()
    ) {
      return true;
    } else {
      return false;
    }
  };

  getSectionHeader = (call: any, index: any) => {
    if (this.callsList && this.callsList.length > 0 && index === 0) {
      return this.callsList[0]["initiatedAt"];
    }

    if (
      this.callsList &&
      this.callsList.length > 0 &&
      index > 0 &&
      CallLogUtils.isDateDifferent(
        this.callsList[index - 1]["initiatedAt"],
        this.callsList[index]["initiatedAt"]
      )
    ) {
      return call.getInitiatedAt();
    }
  };

  onListItemClickCall = (call: any) => {
    let receiverID = call.getReceiver().uid;
    let callType = call?.type;
    let receiverType = call?.receiverType;

    if (this.loggedInUser?.getUid() === call?.getInitiator()?.getUid()) {
      receiverID = call.getReceiver()?.uid;
    } else {
      receiverID = call.getInitiator()?.uid;
    }

    let localCallObj = new CometChat.Call(receiverID, callType, receiverType);

    if (receiverType == CometChat.RECEIVER_TYPE.USER) {
      CometChat.initiateCall(localCallObj)
        .then((outGoingCall) => {
          this.cometchatCallObject = outGoingCall;
          this.showOutgoingCallscreen = true;

          this.ref.detectChanges();
        })
        .catch((error) => {
          if (this.onError) {
            this.onError(error);
          }
        });
    }
  };

  cancelOutgoingCall = () => {
    CometChatSoundManager.pause();
    CometChat.rejectCall(
      this.cometchatCallObject!.getSessionId(),
      CometChatUIKitConstants.calls.cancelled
    )
      .then((call) => {
        this.showOutgoingCallscreen = false;
        CometChatCallEvents.ccCallRejected.next(call);
        this.cometchatCallObject = null;
        this.ref.detectChanges();
      })
      .catch((error) => {
        if (this.onError) {
          this.onError(error);
        }
      });
    this.showOutgoingCallscreen = false;
    this.ref.detectChanges();
  };

  getCallerName(call: any) {
    if (this.loggedInUser?.getUid() === call?.getInitiator()?.getUid()) {
      return call?.getReceiver()?.getName();
    }
    return call.getInitiator()?.getName();
  }
  getAvatarUrl(call: any) {
    if (this.loggedInUser?.getUid() === call?.getInitiator()?.getUid()) {
      return call?.receiver?.avatar || call?.receiver?.icon ;
    }
    return call.initiator?.avatar || call?.initiator?.icon;
  }
  getRequestBuilder() {
    if (this.callLogRequestBuilder) {
      return this.callLogRequestBuilder?.build();
    } else {
      return new CometChatUIKitCalls.CallLogRequestBuilder()
        .setLimit(this.limit)
        .setCallCategory("call")
        .setAuthToken(this.authToken)
        .build();
    }
  }

  getCallTypeIcon(call: any) {
    const missedCall = CallLogUtils.isMissedCall(call, this.loggedInUser!);
    const callType = call.getType();
    let icon;
    if (missedCall) {
      if (callType === CometChat.CALL_TYPE.AUDIO) {
        icon = this.missedAudioCallIconUrl;
      } else {
        icon = this.missedVideoCallIconUrl;
      }
    } else if (call.getInitiator().getUid() === this.loggedInUser!.getUid()) {
      if (callType === CometChat.CALL_TYPE.AUDIO) {
        icon = this.outgoingAudioCallIconUrl;
      } else {
        icon = this.outgoingVideoCallIconUrl;
      }
    } else {
      if (callType === CometChat.CALL_TYPE.AUDIO) {
        icon = this.incomingAudioCallIconUrl;
      } else {
        icon = this.incomingVideoCallIconUrl;
      }
    }
    return icon;
  }

  callLogStyle = () => {
    return {
      height: this.callLogsStyle.height,
      width: this.callLogsStyle.width,
      background: this.callLogsStyle.background,
      border: this.callLogsStyle.border,
      borderRadius: this.callLogsStyle.borderRadius,
    };
  };

  setThemeStyle() {
    this.setAvatarStyle();
    this.setDateStyle();
    this.setCallLogsStyle();
    this.listStyle = {
      titleTextFont: this.callLogsStyle.titleFont,
      titleTextColor: this.callLogsStyle.titleColor,
      emptyStateTextFont: this.callLogsStyle.emptyStateTextFont,
      emptyStateTextColor: this.callLogsStyle.emptyStateTextColor,
      errorStateTextFont: this.callLogsStyle.errorStateTextFont,
      errorStateTextColor: this.callLogsStyle.errorStateTextColor,
      loadingIconTint: this.callLogsStyle.loadingIconTint,
      separatorColor: this.callLogsStyle.dateSeparatorTextColor,

      sectionHeaderTextColor:
        this.callLogsStyle.dateSeparatorTextColor || "rgba(20, 20, 20, 0.46)",
      sectionHeaderTextFont: this.callLogsStyle.dateSeparatorTextFont,
    };
  }
  setAvatarStyle() {
    let defaultStyle: AvatarStyle = new AvatarStyle({
      borderRadius: "24px",
      width: "36px",
      height: "36px",
      border: "none",
      backgroundColor: this.themeService.theme.palette.getAccent700(),
      nameTextColor: this.themeService.theme.palette.getAccent900(),
      backgroundSize: "cover",
      nameTextFont: fontHelper(this.themeService.theme.typography.subtitle1),

      outerViewBorderSpacing: "",
    });
    this.avatarStyle = { ...defaultStyle, ...this.avatarStyle };
  }
  setDateStyle() {
    let defaultStyle: DateStyle = new DateStyle({
      textFont: fontHelper(this.themeService.theme.typography.caption2),
      textColor: this.themeService.theme.palette.getAccent600(),
      background: "transparent",
    });
    this.dateStyle = { ...defaultStyle, ...this.dateStyle };
  }

  getListItemStyle(call: CometChat.Call) {
    let defaultStyle: ListItemStyle = new ListItemStyle({
      height: "45px",
      width: "100%",
      background: this.themeService.theme.palette.getBackground(),
      activeBackground: this.themeService.theme.palette.getAccent100(),
      borderRadius: "0",
      titleFont: fontHelper(this.themeService.theme.typography.title2),
      // titleColor: this.isMissedCall(call, this.loggedInUser!)
      titleColor: CallLogUtils.isMissedCall(call, this.loggedInUser!)
        ? this.themeService.theme.palette.getError()
        : this.themeService.theme.palette.getAccent(),
      border: "none",
      separatorColor: this.themeService.theme.palette.getAccent200(),
      padding: "10px",
      hoverBackground: this.themeService.theme.palette.getAccent50(),
    });
    return { ...defaultStyle, ...this.listItemStyle };
  }

  setCallLogsStyle() {
    let defaultStyle: CallLogsStyle = new CallLogsStyle({
      background: this.themeService.theme.palette.getBackground(),
      border: `1px solid ${this.themeService.theme.palette.getAccent50()}`,
      titleFont: fontHelper(this.themeService.theme.typography.title1),
      titleColor: this.themeService.theme.palette.getAccent(),
      emptyStateTextFont: fontHelper(this.themeService.theme.typography.title1),
      emptyStateTextColor: this.themeService.theme.palette.getAccent600(),
      errorStateTextFont: fontHelper(this.themeService.theme.typography.title1),
      errorStateTextColor: this.themeService.theme.palette.getAccent600(),
      loadingIconTint: this.themeService.theme.palette.getAccent600(),
      dateSeparatorTextColor: this.themeService.theme.palette.getAccent600(),
      dateTextColor: this.themeService.theme.palette.getAccent600(),
      dateTextFont: fontHelper(this.themeService.theme.typography.subtitle2),
      missedCallIconTint: this.themeService.theme.palette.getAccent600(),
      borderRadius: "",
      infoIconTint: this.themeService.theme.palette.getPrimary(),
      outgoingCallIconTint: this.themeService.theme.palette.getAccent600(),
      incomingCallIconTint: this.themeService.theme.palette.getAccent600(),
      callStatusTextFont: fontHelper(
        this.themeService.theme.typography.subtitle2
      ),
      callStatusTextColor: this.themeService.theme.palette.getAccent600(),

      dateSeparatorTextFont: fontHelper(
        this.themeService.theme.typography.subtitle2
      ),
    });

    this.callLogsStyle = { ...defaultStyle, ...this.callLogsStyle };
  }
  subtitleStyle = () => {
    return {
      font: this.callLogsStyle.callStatusTextFont,
      color: this.callLogsStyle.callStatusTextColor,
    };
  };

  infoButtonStyle = () => {
    return {
      height: "24px",
      width: "24px",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      buttonIconTint: this.callLogsStyle.infoIconTint,
    };
  };

  getCallBuilder(): typeof CometChatUIKitCalls.CallSettings | undefined {
    let audioOnlyCall: boolean =
      this.activeCall?.getType() == CometChatUIKitConstants.MessageTypes.audio
        ? true
        : false;
    const callSettings = new CometChatUIKitCalls.CallSettingsBuilder()
      .enableDefaultLayout(true)
      .setIsAudioOnlyCall(audioOnlyCall)
      .setCallListener(
        new CometChatUIKitCalls.OngoingCallListener({
          onCallEnded: () => {
            if (
              this.cometchatCallObject?.getReceiverType() ==
              CometChatUIKitConstants.MessageReceiverType.user
            ) {
              CometChatUIKitCalls.endSession();
              this.closeCallScreen();
            }
          },
          onCallEndButtonPressed: () => {
            if (
              this.cometchatCallObject?.getReceiverType() ==
              CometChatUIKitConstants.MessageReceiverType.user
            ) {
              CometChat.endCall(this.sessionId)
                .then((call: CometChat.Call) => {
                  CometChatUIKitCalls.endSession();
                  CometChatCallEvents.ccCallEnded.next(call);
                  this.closeCallScreen();
                })
                .catch((err: CometChat.CometChatException) => {
                  if (this.onError) {
                    this.onError(err);
                  }
                });
            } else {
              this.closeCallScreen();
            }
          },
          onError: (error: CometChat.CometChatException) => {
            if (this.onError) {
              this.onError(error);
            }
          },
        })
      )
      .build();
    return callSettings;
  }

  closeCallScreen() {
    this.cometchatCallObject = null;
    this.showOngoingCall = false;
    this.sessionId = "";
    this.showOutgoingCallscreen = false;
    this.cometchatCallObject = null;
    this.ref.detectChanges();
  }

  openOngoingCallScreen(call: CometChat.Call) {
    this.showOutgoingCallscreen = false;
    this.cometchatCallObject = call;
    this.sessionId = call.getSessionId();
    this.showOngoingCall = true;
    this.ref.detectChanges();
  }

  setOngoingCallStyle = () => {
    let defaultStyle = new CallscreenStyle({
      maxHeight: "100%",
      maxWidth: "100%",
      border: "none",
      borderRadius: "0",
      background: "#1c2226",
      minHeight: "400px",
      minWidth: "400px",
      minimizeIconTint: this.themeService.theme.palette.getAccent900(),
      maximizeIconTint: this.themeService.theme.palette.getAccent900(),
    });
    this.ongoingCallStyle = { ...defaultStyle, ...this.ongoingCallStyle };
  };

  getDirectionIconStyle(call: any) {
    let tint;
    const missedCall = CallLogUtils.isMissedCall(call, this.loggedInUser!);
    if (missedCall) {
      tint =
        this.callLogsStyle.missedCallIconTint ||
        this.themeService.theme.palette.getAccent600();
    } else if (call.getInitiator().getUid() === this.loggedInUser?.getUid()) {
      tint =
        this.callLogsStyle.outgoingCallIconTint ||
        this.themeService.theme.palette.getAccent600();
    } else {
      tint =
        this.callLogsStyle.incomingCallIconTint ||
        this.themeService.theme.palette.getAccent600();
    }

    return {
      height: "18px",
      width: "18px",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      iconTint: tint,
    };
  }
}
