
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnChanges, OnInit, SimpleChanges, TemplateRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { CometChat } from '@cometchat/chat-sdk-javascript';
import  "@cometchat/uikit-elements";
import '@cometchat/uikit-shared'
import { CometChatCallEvents, CallWorkflow, localize } from '@cometchat/uikit-resources';
import {  CometChatUIKitCalls } from '@cometchat/uikit-shared';

import {CallscreenStyle} from '@cometchat/uikit-elements'
import { CometChatThemeService } from '../../../CometChatTheme.service';
/**
*
* CometChatOngoingCallComponent is a component whic shows outgoing call screen for default audio and video call.
*
* @version 1.0.0
* @author CometChatTeam
* @copyright © 2022 CometChat Inc.
*
*/
@Component({
  selector: "cometchat-ongoing-call",
  templateUrl: "./cometchat-ongoing-call.component.html",
  styleUrls: ["./cometchat-ongoing-call.component.scss"],
})
export class CometChatOngoingCallComponent implements OnInit, OnChanges {
  @ViewChild("callscreenView", { static: false }) callScreenFrame!: ElementRef;
  @Input() ongoingCallStyle: CallscreenStyle = {
    maxHeight: "100%",
    maxWidth: "100%",
    border: "none",
    borderRadius: "0",
    background: "#1c2226",
	  minHeight:"400px",
	  minWidth:"400px",
  }
  @Input() resizeIconHoverText:string = localize("RESIZE")
  @Input() sessionID:string = ""
  @Input() minimizeIconURL:string = "assets/reduce-size.svg"
  @Input() maximizeIconURL:string = "assets/increase-size.svg"
  @Input() callSettingsBuilder!:typeof CometChatUIKitCalls.CallSettingsBuilder;
  @Input() callWorkflow:CallWorkflow = CallWorkflow.directCalling;
  @Input() onError: (error: CometChat.CometChatException) => void = (error: CometChat.CometChatException) => {
    console.log(error)
  }
  public loggedInUser!:CometChat.User;
  ngOnInit(): void {
    this.setongoingCallStyle()
  }
  constructor(private themeService:CometChatThemeService){
  }
  ngOnChanges(changes: SimpleChanges): void {
   if(changes && changes["sessionID"].currentValue){
    this.startCall()
   }
  }
   getCallBuilder = ():any => {
if(this.callSettingsBuilder){
	return this.callSettingsBuilder
}
else{
	const callSettings:any = new CometChatUIKitCalls.CallSettingsBuilder()
	.enableDefaultLayout(true)
	.setIsAudioOnlyCall(false)
	.setCallListener(
	  new CometChatUIKitCalls.OngoingCallListener({
		onCallEnded: () => {
			CometChatUIKitCalls.endSession()
			if(this.callWorkflow == CallWorkflow.defaultCalling){
				CometChatUIKitCalls.endCall();
				CometChatCallEvents.ccCallEnded.next({} as CometChat.Call)

			}
		},
		onCallEndButtonPressed: () => {
			if(this.callWorkflow == CallWorkflow.defaultCalling){
				CometChat.endCall(this.sessionID).then((call:CometChat.Call)=>{
					CometChatUIKitCalls.endSession()
					CometChatCallEvents.ccCallEnded.next(call);
				  })
				  .catch((error:CometChat.CometChatException)=>{
					if(this.onError){
						this.onError(error)
					 }
				  })
			}
			else{
				CometChatCallEvents.ccCallEnded.next({} as CometChat.Call);
				CometChatUIKitCalls.endSession();
			}

		  },
		onError: (error:CometChat.CometChatException) => {
		 if(this.onError){
			this.onError(error)
		 }
		},
	  })
	)
	.build();
	return callSettings
}
  }
startCall = () => {
	if(this.loggedInUser){
		const authToken = this.loggedInUser!.getAuthToken();
		const sessionID = this.sessionID;
		CometChatUIKitCalls.generateToken(sessionID, authToken).then(
			(res:any) => {
				CometChatUIKitCalls.startSession(
					res?.token,
					this.getCallBuilder(),
					this.callScreenFrame.nativeElement
				)
				.catch((error:CometChat.CometChatException)=>{
					if(this.onError){
						this.onError(error)
					 }
				})
			},
			(err:CometChat.CometChatException) => {
				if(this.onError){
					this.onError(err)
				 }
			}
		)
		.catch((error:CometChat.CometChatException)=>{
			if(this.onError){
				this.onError(error)
			 }
		})
	}
	else{
		CometChat.getLoggedinUser().then((user:CometChat.User | null)=>{
			const authToken = user!.getAuthToken();
			const sessionID = this.sessionID;
			CometChatUIKitCalls.generateToken(sessionID, authToken).then(
				(res:any) => {
					CometChatUIKitCalls.startSession(
						res?.token,
					 this.getCallBuilder(),
           this.callScreenFrame.nativeElement
					);
				},
				(error:CometChat.CometChatException) => {
					if(this.onError){
						this.onError(error)
					 }
				}
			)
			.catch((error:CometChat.CometChatException)=>{
				if(this.onError){
					this.onError(error)
				 }
			})
		})
	}
};
setongoingCallStyle(){
  let defaultStyle = new CallscreenStyle({
    maxHeight: "100%",
    maxWidth: "100%",
    border: "none",
    borderRadius: "0",
    background: "#1c2226",
	minHeight:"400px",
	minWidth:"400px",
	minimizeIconTint:this.themeService.theme.palette.getAccent("dark"),
	maximizeIconTint:this.themeService.theme.palette.getAccent("dark"),
  });
  this.ongoingCallStyle = {...defaultStyle,...this.ongoingCallStyle}
}
}
