import {
  Component,
  OnInit,
  Input,
  ChangeDetectionStrategy,
  OnChanges,
  SimpleChanges,
  ChangeDetectorRef,
  ElementRef,
} from "@angular/core";
import { CometChat } from "@cometchat/chat-sdk-javascript";

import {
  fontHelper,
  localize,
  CometChatUIKitConstants,
} from "@cometchat/uikit-resources";

import "@cometchat/uikit-elements";
import { CometChatThemeService } from "../../../CometChatTheme.service";
import { BackdropStyle } from "@cometchat/uikit-elements";
import {
  CallLogDetailsConfiguration,
  CallLogsConfiguration,
  WithDetailsStyle,
} from "@cometchat/uikit-shared";

@Component({
  selector: "cometchat-call-logs-with-details",
  templateUrl: "./cometchat-call-logs-with-details.component.html",
  styleUrls: ["./cometchat-call-logs-with-details.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CometChatCallLogsWithDetailsComponent
  implements OnInit, OnChanges
{
  @Input() isMobileView: boolean = false;
  @Input() messageText: string = localize("NO_CALLS_SELECTED");
  @Input() withDetailsStyle: WithDetailsStyle = {};
  @Input() showMoreInfo: boolean = true;
  @Input() backdropStyle: BackdropStyle = {};
  @Input() call!: any;

  @Input() callLogDetailsConfiguration: CallLogDetailsConfiguration =
    new CallLogDetailsConfiguration({});

  @Input() callLogConfiguration: CallLogsConfiguration =
    new CallLogsConfiguration({});

  @Input() onError: ((error: CometChat.CometChatException) => void) | null = (
    error: CometChat.CometChatException
  ) => {
    console.log(error);
  };
  /**
   * Properties for internal use
   */
  public loggedInUser!: CometChat.User | null;
  public activeCall!:any;
  user!: any | null;
  group!: CometChat.Group | null;

  computedCallLogDetailsConfig: CallLogDetailsConfiguration =
    new CallLogDetailsConfiguration({});

  labelStyle: any = {
    background: "transparent",
    textFont: "700 22px Inter",
    textColor: "rgba(20, 20, 20, 0.33)",
  };

  constructor(
    private elementRef: ElementRef,
    private ref: ChangeDetectorRef,
    private themeService: CometChatThemeService
  ) {}
  ngOnChanges(changes: SimpleChanges): void {
    if (changes["user"] || changes["group"]) {
      this.setActiveCallLog();
    }

    if (changes["isMobileView"]) {
      if (this.isMobileView) {
        this.backdropStyle.height = "100%";
        this.backdropStyle.width = "100%";
        this.computedCallLogDetailsConfig =
          this.computedCallLogDetailsConfiguration();
      } else if (!this.isMobileView) {
        this.backdropStyle.height = "100%";
        this.backdropStyle.width = "100%";
        this.computedCallLogDetailsConfig =
          this.computedCallLogDetailsConfiguration();
      }
      setTimeout(() => {
        this.updateBackdropHeight();
      }, 100);
    }
  }

  updateBackdropHeight() {
    const divHeight = this.elementRef.nativeElement.offsetHeight;
    const divWidth = this.elementRef.nativeElement.offsetWidth;
    this.backdropStyle = {
      height: divHeight + "px",
      width: divWidth + "px",
      background: "rgba(0, 0, 0, 0.5)",
      position: "fixed",
    };

    if (this.isMobileView) {
      this.backdropStyle.height = divHeight + "px";
      this.backdropStyle.width = divWidth + "px";
    } else {
      this.backdropStyle.height = divHeight + "px";
      this.backdropStyle.width = divWidth + "px";
    }

    this.backdropStyle.height = divHeight + "px";
    this.backdropStyle.width = divWidth + "px";
  }

  onBackClick = () => {
    this.user = null;
    this.group = null;
    this.activeCall = null;
  };
  setWithDetailsStyle() {
    let defaultStyle: WithDetailsStyle = new WithDetailsStyle({
      width: "100%",
      height: "100%",
      background: this.themeService.theme.palette.getBackground(),
      borderRadius: "none",
      border: "none",
      messageTextColor: this.themeService.theme.palette.getAccent600(),
      messageTextFont: fontHelper(this.themeService.theme.typography.title1),
    });
    this.withDetailsStyle = {
      ...defaultStyle,
      ...this.withDetailsStyle,
    };
    this.labelStyle.textFont = this.withDetailsStyle.messageTextFont;
    this.labelStyle.textColor = this.withDetailsStyle.messageTextColor;
  }

  public onInfoClick: (call: any) => void = (call: any) => {
    this.call = call;
    this.setActiveCallLog();
  };
  setActiveCallLog() {
    this.activeCall = this.call;
    if (this.call!.getInitiator().getUid() == this.loggedInUser?.getUid()) {
      if (
        this.call!.getReceiverType() ==
        CometChatUIKitConstants.MessageReceiverType.user
      ) {
        this.user = this.call!.getReceiver() as unknown as CometChat.User;
        this.group = null;
      } else {
        this.user = null;
        this.group = this.call!.getReceiver() as unknown as CometChat.Group;
      }
    } else {
      this.user = this.call!.getInitiator() as unknown as CometChat.User;
    }
  }
  ngOnInit() {
    this.computedCallLogDetailsConfig =
      this.computedCallLogDetailsConfiguration();
    this.updateBackdropHeight();
    this.setWithDetailsStyle();
    CometChat.getLoggedinUser()
      .then((user: CometChat.User | null) => {
        this.loggedInUser = user;
      })
      .catch((error: CometChat.CometChatException) => {
        if (this.onError) {
          this.onError(error);
        }
      });
  }
  computedCallLogDetailsConfiguration(): CallLogDetailsConfiguration {
    const config = new CallLogDetailsConfiguration({});
    Object.assign(config, this.callLogDetailsConfiguration);

    config.callLogDetailsStyle = {
      ...config.callLogDetailsStyle,
      border: `1px solid ${this.themeService.theme.palette?.getAccent100()}`,
    };

    if (this.isMobileView)
      config.onBackClick = () => {
        this.activeCall = null;
        this.user = null;
        this.group = null;
      };

    return config;
  }
  emptyMessageStyle = () => {
    return {
      background:
        this.withDetailsStyle.background ||
        this.themeService.theme.palette.getBackground(),
      height: this.withDetailsStyle.height,
      width: `calc(${this.withDetailsStyle.width} - 280px)`,
      border: this.withDetailsStyle.border,
      borderRadius: this.withDetailsStyle.borderRadius,
    };
  };
  chatsWrapperStyles = () => {
    return {
      height: this.withDetailsStyle.height,
      width: this.withDetailsStyle.width,
      border: this.withDetailsStyle.border,
      borderRadius: this.withDetailsStyle.borderRadius,
      background:
        this.withDetailsStyle.background ||
        this.themeService.theme.palette.getBackground(),
    };
  };
}
