import {
  Component,
  Input,
  OnInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  TemplateRef,
  OnChanges,
  SimpleChanges,
} from "@angular/core";
import "@cometchat/uikit-elements";
import {
  AvatarStyle,
  DateStyle,
  IconStyle,
  ListItemStyle,
} from "@cometchat/uikit-elements";
import {
  CallButtonsStyle,
  CallButtonsConfiguration,
  CallLogDetailsStyle,
  CallLogParticipantsConfiguration,
  CallLogHistoryConfiguration,
  CallLogRecordingsConfiguration,
} from "@cometchat/uikit-shared";
import {
  fontHelper,
  localize,
  CometChatUIKitConstants,
  CometChatDetailsOption,
  CometChatDetailsTemplate,
  DatePatterns,
  CometChatCallDetailsOption,
} from "@cometchat/uikit-resources";
import { CometChat } from "@cometchat/chat-sdk-javascript";
import { CometChatThemeService } from "../../../CometChatTheme.service";
import { CallDetailUtils } from "../../../Shared/Utils/CallDetailUtils"
import { CallLogUtils } from "../../../Shared/Utils/CallLogUtils";

@Component({
  selector: "cometchat-call-log-details",
  templateUrl: "./cometchat-call-log-details.component.html",
  styleUrls: ["./cometchat-call-log-details.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CometChatCallLogDetailsComponent implements OnInit, OnChanges {
  @Input() call!: any;
  @Input() group!: CometChat.Group;
  @Input() user!: CometChat.User;
  @Input() title: string = localize("CALL_DETAILS");
  @Input() onBackClick!: () => void;
 
  @Input() hideProfile: boolean = false;
  @Input() subtitleView!: TemplateRef<any>;
  @Input() customProfileView!: TemplateRef<any>;

  @Input() backIconUrl: string = "assets/backbutton.svg";
  @Input() greaterThanIconURL: string = "assets/greaterThanIcon.svg";

  @Input() callButtonsConfiguration: CallButtonsConfiguration =
    new CallButtonsConfiguration({});

  @Input() callLogParticipantsConfiguration: CallLogParticipantsConfiguration =
    new CallLogParticipantsConfiguration({});

  @Input() callLogHistoryConfiguration: CallLogHistoryConfiguration =
    new CallLogHistoryConfiguration({});

  @Input() callLogRecordingsConfiguration: CallLogRecordingsConfiguration =
    new CallLogRecordingsConfiguration({});

  @Input() onError: ((error: CometChat.CometChatException) => void) | null = (
    error: CometChat.CometChatException
  ) => {
    console.log(error);
  };
  @Input() datePattern: DatePatterns = DatePatterns.time;
  @Input() datePattern2: DatePatterns = DatePatterns.DayDate;

  @Input() data: CometChatDetailsTemplate[] = [];

  @Input() avatarStyle: AvatarStyle = {
    borderRadius: "16px",
    width: "28px",
    height: "28px",
    border: "none",
  };
  @Input() callDetailsStyle: CallLogDetailsStyle = {
    width: "100%",
    height: "100%",
  };

  @Input() listItemStyle: ListItemStyle = {};

  @Input() dateStyle: DateStyle = {};
  @Input() callButtonsStyle: CallButtonsStyle = {
    width: "100%",
    height: "100%",
    border: "none",
    borderRadius: "0",
    background: "transparent",
  };

  iconStyle: IconStyle = {
    height: "16px",
    width: "16px",
    iconTint: "RGBA(20, 20, 20, 0.68)",
  };

  defaultTemplate: CometChatDetailsTemplate[] = [];

  public authToken: string = "";
  public loggedInUser: CometChat.User | null = null;
  public showCallLogDetailOptionList: Boolean = true;
  public showCometChatMessages: Boolean = true;
  public showParticipantsList: Boolean = false;
  public showCallRecordingList: Boolean = false;
  public showCallHistory: Boolean = false;

  dividerStyle: any = {
    background: "rgb(222 222 222 / 46%)",
    height: "1px",
    width: "100%",
  };

  getTitleStyle() {
    return {
      textFont:
        this.callDetailsStyle.titleFont ||
        fontHelper(this.themeService.theme.typography.title1),
      textColor:
        this.callDetailsStyle.titleColor ||
        this.themeService.theme.palette.getAccent(),
    };
  }
  public userListenerId = "userlist_" + new Date().getTime();
  public requestBuilder: any;
  public limit: number = 5;

  public onItemClick: (call: any) => void = (call: any) => {
    this.call = call;
    this.showCallLogDetailOptionList = true;
    this.showCallHistory = false;
    this.ref.detectChanges();
  };
  types: string[] = [];
  categories: string[] = [];
  constructor(
    private ref: ChangeDetectorRef,
    private themeService: CometChatThemeService
  ) {}
  ngOnChanges(changes: SimpleChanges): void {
    if (changes["user"] || changes["group"]) {
      this.showCallLogDetailOptionList = true;
      this.showParticipantsList = false;
      this.showCallRecordingList = false;
      this.showCallHistory = false;
      this.showCometChatMessages = false;
    }
  }

  removeListener() {
    CometChat.removeUserListener(this.userListenerId);
  }

  ngOnInit(): void {
    CometChat.getLoggedinUser()
      .then((user: CometChat.User | null) => {
        this.loggedInUser = user;
        this.authToken = this.loggedInUser!.getAuthToken();
        this.getTemplate();
      })
      .catch((error: CometChat.CometChatException) => {
        if (this.onError) {
          this.onError(error);
        }
      });

    this.setThemeStyle();
  }

  trackByFnOption(index: number, option: any): string {
    return option.id;
  }
  getTemplate() {
    if (this.data && this.data?.length > 0) {
      this.defaultTemplate = this.data;
      this.ref.detectChanges();
    } else {
      this.defaultTemplate = CallDetailUtils.getDefaultCallTemplate(
        this.call,
        this.loggedInUser!,
        this.themeService.theme
      );

      this.ref.detectChanges();
    }
  }

  getTemplateOptions = (template: CometChatDetailsTemplate) => {
    if (template.options) {
      return template.options(this.user, this.group, template.id as string);
    } else return [];
  };

  getCustomOptionView(option: CometChatDetailsOption) {
    return option?.customView;
  }

  getSectionHeaderStyle(template: CometChatDetailsTemplate) {
    return {
      textFont: template.titleFont,
      textColor: template.titleColor,
    };
  }

  onOptionClick = (option: CometChatCallDetailsOption) => {
    if (option?.onClick) {
      option.onClick(this.call!);
    } else {
      switch (option.id) {
        case "participants":
          this.showCallLogDetailOptionList = false;
          this.showParticipantsList = true;
          this.ref.detectChanges()
          break;
        case "recordings":
          this.showCallLogDetailOptionList = false;

          this.showCallRecordingList = true;
          this.ref.detectChanges()

          break;
        case "callHistory":
          this.showCallLogDetailOptionList = false;

          this.showCallHistory = true;
          this.ref.detectChanges()

          break;
        default:
          break;
      }
    }
  };

  getButtonStyle(option: CometChatDetailsOption) {
    return {
      height: "100%",
      width: "100%",
      border: "none",
      borderRadius: "0",
      buttonTextFont: option?.titleFont,
      buttonTextColor: option?.titleColor,
      background: option?.backgroundColor || "transparent",
    };
  }


  handlePageOnBackClick = () => {
    this.showCallHistory = false;
    this.showCallRecordingList = false;
    this.showParticipantsList = false;
    this.showCometChatMessages = false;
    this.showCallLogDetailOptionList = true;
    this.ref.detectChanges();
  };

  subtitleStyle(template: CometChatDetailsTemplate) {
    return {
      textFont: template.titleFont,
      textColor: template.titleColor,
    };
  }
  showDataSectionStyle(template: CometChatDetailsTemplate) {
    return {
      textFont: template.titleFont,
      textColor: template.titleColor,
    };
  }

  setThemeStyle() {
    this.setDetailsStyle();
    this.setAvatarStyle();
    this.setListItemStyle();
    this.setDateStyle();
  }
  setListItemStyle() {
    let defaultStyle: ListItemStyle = new ListItemStyle({
      height: "45px",
      width: "100%",
      background:
        this.themeService.theme.palette.getAccent50() ||
        this.themeService.theme.palette.getBackground(),
      activeBackground: "transparent",
      borderRadius: "5px",
      titleFont: fontHelper(this.themeService.theme.typography.title2),
      titleColor: this.themeService.theme.palette.getAccent(),
      border: "none",
      separatorColor: this.themeService.theme.palette.getAccent200(),
      hoverBackground: this.themeService.theme.palette.getAccent100(),
    });
    this.listItemStyle = { ...defaultStyle, ...this.listItemStyle };
  }
  setAvatarStyle() {
    let defaultStyle: AvatarStyle = new AvatarStyle({
      borderRadius: "24px",
      width: "46px",
      height: "46px",
      border: "none",
      backgroundColor: this.themeService.theme.palette.getAccent700(),
      nameTextColor: this.themeService.theme.palette.getAccent900(),
      backgroundSize: "cover",
      nameTextFont: fontHelper(this.themeService.theme.typography.subtitle1),

      outerViewBorderSpacing: "",
    });
    this.avatarStyle = { ...defaultStyle, ...this.avatarStyle };
  }

  setDetailsStyle() {
    let defaultStyle: CallLogDetailsStyle = new CallLogDetailsStyle({
      background: this.themeService.theme.palette.getBackground(),
      titleFont: fontHelper(this.themeService.theme.typography.title1),
      titleColor: this.themeService.theme.palette.getAccent(),
      backIconTint: this.themeService.theme.palette.getPrimary(),
      width: "100%",
      height: "100%",
      borderRadius: "",
      nameTextFont: fontHelper(this.themeService.theme.typography.subtitle2),
    });
    this.callDetailsStyle = { ...defaultStyle, ...this.callDetailsStyle };
  }

  setDateStyle() {
    let defaultStyle: DateStyle = new DateStyle({
      textFont: fontHelper(this.themeService.theme.typography.caption2),
      textColor: this.themeService.theme.palette.getAccent600(),
      background: "transparent",
    });
    this.dateStyle = { ...defaultStyle, ...this.dateStyle };
  }
  wrapperStyle = () => {
    return {
      width: this.callDetailsStyle.width,
      height: this.callDetailsStyle.height,
      border: this.callDetailsStyle.border,
      borderRadius: this.callDetailsStyle.borderRadius,
      background: this.callDetailsStyle.background,
    };
  };

  getTailView(totalSeconds: number) {
    return CallLogUtils.convertMinutesToHoursMinutesSeconds(totalSeconds);
  }

  getSubtitle(call: any): string {
    return CallLogUtils.getCallStatusWithType(call, this.loggedInUser!);
  }

  backButtonStyle = () => {
    return {
      height: "24px",
      width: "24px",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      buttonIconTint: this.themeService.theme.palette.getPrimary(),
    };
  };
  getListItemStyle(option: CometChatDetailsOption) {
    return {
      height: "auto",
      width: "100%",
      background:
        option?.backgroundColor ||
        this.themeService.theme.palette.getBackground(),
      borderRadius: "0",
      titleFont:
        option?.titleFont ||
        fontHelper(this.themeService.theme.typography.title2),

      border: "none",
      separatorColor: this.themeService.theme.palette.getAccent200(),
      hoverBackground: this.themeService.theme.palette.getAccent100(),
    };
  }
}
