import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Input,
  OnChanges,
  SimpleChanges,
  TemplateRef,
} from "@angular/core";

import { CometChat } from "@cometchat/chat-sdk-javascript";
import { ListStyle, BaseStyle } from "@cometchat/uikit-shared";
import {
  AvatarStyle,
  DateStyle,
  ListItemStyle,
} from "@cometchat/uikit-elements";
import { CometChatThemeService } from "../../../CometChatTheme.service";
import {
  fontHelper,
  DatePatterns,
  States,
  localize,
} from "@cometchat/uikit-resources";
import { CallLogParticipantsStyle } from "@cometchat/uikit-shared";
import { CallLogUtils } from "../../../Shared/Utils/CallLogUtils";

@Component({
  selector: "cometchat-call-log-participants",
  templateUrl: "./cometchat-call-log-participants.component.html",
  styleUrls: ["./cometchat-call-log-participants.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CometChatCallLogParticipantsComponent implements OnInit {
  @Input() title: string = localize("PARTICIPANTS");
  @Input() call!: any;
  @Input() backIconUrl: string = "assets/backbutton.svg";
  @Input() onBackClick!: () => void;
  @Input() datePattern: DatePatterns = DatePatterns.DayDateTime;
  @Input() subtitleView!: any;
  @Input() listItemView!: any;
  @Input() onError: (error: CometChat.CometChatException) => void = (
    error: CometChat.CometChatException
  ) => {
    console.log(error);
  };

  @Input() hideSeparator: boolean = false;
  @Input() avatarStyle: AvatarStyle = {
    borderRadius: "16px",
    width: "32px",
    height: "32px",
  };
  @Input() dateStyle: DateStyle = {};
  @Input() CallLogParticipantsStyle: CallLogParticipantsStyle = {
    width: "100%",
    height: "100%",
  };
  @Input() listItemStyle: ListItemStyle = {};

  public participantsList: any = [];
  public loggedInUser: CometChat.User | null = null;
  public state: States = States.loaded;
  listStyle: ListStyle = {};
  public limit: number = 30;

  constructor(
    private ref: ChangeDetectorRef,
    private themeService: CometChatThemeService
  ) {
    this.state = States.loading;
  }

  ngOnInit(): void {

    this.setThemeStyle();
   
    CometChat.getLoggedInUser()?.then((user: CometChat.User | null) => {
        this.participantsList = this.call?.getParticipants();
       this.ref.detectChanges()
        this.state = States.loaded;
        this.loggedInUser = user;
      })
      .catch((error: CometChat.CometChatException) => {
        if (this.onError) {
          this.onError(error);
        }
      });
  }

  getTailView(totalSeconds: any) {
    return CallLogUtils.convertMinutesToHoursMinutesSeconds(totalSeconds);
  }
  callStyle = () => {
    return {
      height: this.CallLogParticipantsStyle.height,
      width: this.CallLogParticipantsStyle.width,
      background: this.CallLogParticipantsStyle.background,
      border: this.CallLogParticipantsStyle.border,
      borderRadius: this.CallLogParticipantsStyle.borderRadius,
    };
  };
  setThemeStyle() {
    this.setAvatarStyle();
    this.setDateStyle();
    this.setCallsStyle();
    this.listStyle = {
      titleTextFont: this.CallLogParticipantsStyle.titleFont,
      titleTextColor: this.CallLogParticipantsStyle.titleColor,
    };
  }
  setAvatarStyle() {
    let defaultStyle: AvatarStyle = new AvatarStyle({
      borderRadius: "24px",
      width: "36px",
      height: "36px",
      border: "none",
      backgroundColor: this.themeService.theme.palette.getAccent700(),
      nameTextColor: this.themeService.theme.palette.getAccent900(),
      backgroundSize: "cover",
      nameTextFont: fontHelper(this.themeService.theme.typography.subtitle1),
      outerViewBorderSpacing: "",
    });
    this.avatarStyle = { ...defaultStyle, ...this.avatarStyle };
  }
  setDateStyle() {
    let defaultStyle: DateStyle = new DateStyle({
      textFont: fontHelper(this.themeService.theme.typography.caption2),
      textColor: this.themeService.theme.palette.getAccent600(),
      background: "transparent",
    });
    this.dateStyle = { ...defaultStyle, ...this.dateStyle };
  }

  getListItemStyle() {
    let defaultStyle: ListItemStyle = new ListItemStyle({
      height: "45px",
      width: "100%",
      background: this.themeService.theme.palette.getBackground(),
      activeBackground: this.themeService.theme.palette.getAccent100(),
      borderRadius: "0",
      titleFont: fontHelper(this.themeService.theme.typography.title2),
      titleColor: this.themeService.theme.palette.getAccent(),
      border: "none",
      separatorColor: this.themeService.theme.palette.getAccent50(),
      hoverBackground: this.themeService.theme.palette.getAccent50(),
    });
    return { ...defaultStyle, ...this.listItemStyle };
  }

  setCallsStyle() {
    let defaultStyle: CallLogParticipantsStyle = new CallLogParticipantsStyle({
      titleFont: fontHelper(this.themeService.theme.typography.title1),
      titleColor: this.themeService.theme.palette.getAccent(),
      callStatusFont: fontHelper(this.themeService.theme.typography.subtitle2),
      callStatusColor: this.themeService.theme.palette.getAccent600(),
      background: this.themeService.theme.palette.getBackground(),
      border: `1px solid ${this.themeService.theme.palette.getAccent50()}`,
      backIconTint: this.themeService.theme.palette.getPrimary(),
    });
    this.CallLogParticipantsStyle = {
      ...defaultStyle,
      ...this.CallLogParticipantsStyle,
    };
  }
  subtitleStyle = () => {
    return {
      font: this.CallLogParticipantsStyle.callStatusFont,
      color: this.CallLogParticipantsStyle.callStatusColor,
    };
  };
  titleStyle = () => {
    return {
      font: this.CallLogParticipantsStyle.titleFont,
      color: this.CallLogParticipantsStyle.titleColor,
      background: "transparent",
    };
  };

  handleBackClick = () => {
    if (this.onBackClick) {
      this.onBackClick();
    }
  };

  backButtonStyle = () => {
    return {
      height: "24px",
      width: "24px",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      buttonIconTint:
        this.CallLogParticipantsStyle.backIconTint ||
        this.themeService.theme.palette.getPrimary(),
    };
  };
}
